:root {
  --clr-primary-1: rgba(36, 5, 62, 1);
  --clr-primary-2: rgba(68, 255, 161, 1);
  --clr-neutral-1: rgba(88, 77, 98, 1);
  --clr-neutral-2: rgba(252, 248, 255, 1);
  --clr-neutral-3: rgba(255, 255, 255, 1);
}

@font-face {
  font-family: "Fraunces";
  src: url("/assets/fonts/fraunces/static/Fraunces_144pt-SemiBold.ttf") format("TrueType");
  font-weight: 600;
}
@font-face {
  font-family: "Manrope";
  src: url("/assets/fonts/manrope/static/Manrope-Regular.ttf") format("TrueType");
  font-weight: 400;
}
:root {
  --ff-fraunces: "Fraunces", serif;
  --ff-manrope: "Manrope", sans-serif;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

html {
  overflow-x: hidden;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  --fs-large: 3.125rem;
  --fs-medium: 2rem;
  --fs-small: 1.75rem;
  --fs-body: 1rem;
  --lh-large: 1;
  --lh-medium: 1.14;
  --lh-small: 1.25;
  --lh-body: 1.78;
}

@media (min-width: 45rem) {
  :root {
    --fs-large: 3.75rem;
    --fs-medium: 3rem;
  }
}
@media (min-width: 75rem) {
  :root {
    --fs-large: 5rem;
    --fs-medium: 3.5rem;
    --fs-small: 2rem;
    --fs-body: 1.125rem;
  }
}
body {
  font-size: var(--fs-body);
  line-height: var(--lh-body);
  font-family: var(--ff-manrope);
}

.wrapper {
  width: 100%;
  max-width: 69.5rem;
  margin-inline: auto;
  padding-inline: 1rem;
}
@media (min-width: 45rem) {
  .wrapper {
    padding-inline: 2.5rem;
  }
}
@media (min-width: 75rem) {
  .wrapper {
    padding-inline: 0;
  }
}

/* Hiding class, making content visible only to screen readers but not visually */
/* "sr" meaning "screen-reader" */
.sr-only:not(:focus):not(:active) {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

.button-cta--1 {
  position: relative;
  color: var(--clr-neutral-3);
  font-weight: 700;
  transition: color 200ms ease-in-out;
}
.button-cta--1:hover {
  color: var(--clr-primary-2);
}
.button-cta--1::after {
  content: "";
  position: absolute;
  bottom: -8px;
  left: 0;
  height: 3px;
  width: 100%;
  background-color: var(--clr-primary-2);
}

.button-cta--2 {
  color: var(--clr-primary-1);
  font-weight: 700;
  display: inline-block;
  padding: 0.8125rem 1.5625rem 1rem;
  background-color: var(--clr-primary-2);
  border: 0.125rem solid var(--clr-primary-2);
  text-decoration: none;
  transition: all 200ms ease-in-out;
}
.button-cta--2:hover {
  color: var(--clr-primary-2);
  background-color: var(--clr-primary-1);
}

.header {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: bottom;
  justify-content: space-between;
  padding-top: 2.3125rem;
  z-index: 2;
}
@media (min-width: 75rem) {
  .header {
    padding-top: 3.61125rem;
  }
}
.header__nav ul {
  list-style: none;
}
.header__button {
  text-decoration: none;
}

.hero {
  position: relative;
  padding-top: 8.125rem;
  padding-bottom: 9.75rem;
  text-align: center;
  background-color: var(--clr-primary-1);
  border-radius: 0% 1% 50% 50%/0% 0% 5% 5%;
}
@media (min-width: 45rem) {
  .hero {
    padding-top: 7.625rem;
    padding-bottom: 12.6875rem;
  }
}
@media (min-width: 75rem) {
  .hero {
    padding-top: 9.125rem;
    padding-bottom: 20.375rem;
  }
}
.hero__title {
  color: var(--clr-neutral-3);
  font-size: var(--fs-large);
  line-height: var(--lh-large);
  font-family: var(--ff-fraunces);
  margin-bottom: 2.5rem;
}
.hero__title:nth-of-type(1) {
  white-space: pre-wrap;
}
.hero__title:nth-of-type(2) {
  white-space: pre-line;
}
.hero__title span {
  position: relative;
}
.hero__title span::after {
  content: "";
  position: absolute;
  bottom: 3px;
  left: 0;
  height: 3px;
  width: 100%;
  background-color: var(--clr-primary-2);
}
.hero__pattern {
  display: none;
}
@media (min-width: 45rem) {
  .hero__pattern {
    display: block;
    position: absolute;
  }
}
.hero__pattern--1 {
  top: 4.4375rem;
  right: 100%;
  transform: translateX(33%);
}
@media (min-width: 75rem) {
  .hero__pattern--1 {
    top: 7.25rem;
    transform: translateX(58%);
  }
}
.hero__pattern--2 {
  top: 15.625rem;
  left: 100%;
  transform: translateX(-50%);
}
@media (min-width: 75rem) {
  .hero__pattern--2 {
    top: 19.625rem;
    transform: translateX(-72%);
  }
}
.hero__img {
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 18.625rem;
}
@media (min-width: 45rem) {
  .hero__img {
    max-width: 32.125rem;
  }
}
@media (min-width: 75rem) {
  .hero__img {
    max-width: 47.9375rem;
    transform: translate(-50%, -60%);
  }
}

.features {
  padding-block: 9.6875rem 4.4375rem;
  background-color: var(--clr-neutral-2);
  border-radius: 0% 1% 50% 50%/0% 0% 5% 5%;
}
@media (min-width: 45rem) {
  .features {
    padding-block: 12.6875rem 6.4375rem;
  }
}
@media (min-width: 75rem) {
  .features {
    padding-block: 15.9375rem 13.5625rem;
  }
}
.features__list {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
}
@media (min-width: 45rem) {
  .features__list {
    gap: 3rem;
  }
}
@media (min-width: 75rem) {
  .features__list {
    flex-direction: row;
    gap: 1.625rem;
  }
}
.features__item {
  text-align: center;
}
@media (min-width: 45rem) {
  .features__item {
    text-align: left;
    display: flex;
    align-items: center;
    gap: 2rem;
  }
}
@media (min-width: 75rem) {
  .features__item {
    text-align: center;
    flex-direction: column;
    flex: 1;
    gap: 0;
  }
}
.features__counter {
  font-size: 1.25rem;
  font-weight: 600;
  line-height: 36px;
  font-family: var(--ff-fraunces);
  color: var(--clr-primary-1);
  padding: 0.3125rem 1.2rem;
  border: 1px solid var(--clr-primary-1);
  width: fit-content;
  border-radius: 50%;
  margin-inline: auto;
  margin-bottom: 1.5rem;
}
@media (min-width: 45rem) {
  .features__counter {
    margin-bottom: 0;
  }
}
@media (min-width: 75rem) {
  .features__counter {
    margin-bottom: 3.5rem;
    font-size: 1.5rem;
  }
}
.features__title {
  font-family: var(--ff-fraunces);
  font-size: var(--fs-small);
  color: var(--clr-primary-1);
  line-height: var(--lh-small);
  margin-bottom: 1rem;
}
@media (min-width: 75rem) {
  .features__title {
    margin-bottom: 1.75rem;
  }
}
.features__description {
  font-size: var(--fs-body);
  color: var(--clr-neutral-1);
}